@use '../../design/design';

#form-container {

    display: flex;
    flex-direction: column;
    width: 100%;

    #form {

        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 10px;
        width: 100%;
        font-family: design.$primary-font;

        #url-input {

            display: flex;
            align-items: center;
            margin-bottom: 10px;
            width: 100%;
            max-width: 500px;
            min-width: 350px;


            label {

                padding-right: 5px;

            }


            input {

                text-decoration: none;
                width: 100%;

            }

        }


        ul {

            display: flex;
            list-style: none;
            align-items: flex-start;
            justify-content: center;
            width: 60%;
            max-width: 350px;

            li {

                box-sizing: border-box;
                border-radius: 5px;
                border: 1px solid design.$li-border-color;
                padding: 2%;
                margin: 1% 3%;
                background-color: design.$li-background-color-inactive;

            }


            li.activeButton {

                background-color: design.$li-background-color-active;

            }


            li:hover {

                background-color: design.$li-hover;

            }

        }

    }

}
